.pageTitle {
    width: 100%;
    z-index: 2;
    
    text-align: center;
    font-size: x-large;
}

.pageHeader {
    background-color: #4f3565;
    margin: 0;
    padding: 1vh 0;
}

.pageHeader > nav > ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.pageHeader > nav > ul > li {
    font-size: large;
    flex: 0 0 33%;
    text-align: center;
}